import React, { Component } from 'react'
import LyricsIcon from '@mui/icons-material/Lyrics';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import { Consumer } from '../../Context';
import axios from 'axios';




class Search extends Component {

  state={
      trackTitle:''
  }


   onChange =(e)=>{
      this.setState({trackTitle:e.target.value});
  }
   clearSearch =()=>{
    this.setState({trackTitle:''})
  }
   findTrack =(dispatch,e)=>{
    e.preventDefault();
    axios.request(
      {
        method: 'GET',
        url: 'https://cors-proxy4.p.rapidapi.com/',
        params: {
          url: `https://api.musixmatch.com/ws/1.1/track.search?q_artist=${this.state.trackTitle}&page_size=10&page=1&s_track_rating=desc&apikey=${process.env.REACT_APP_MM_KEY}`
        },
        headers: {
          'X-RapidAPI-Key': '1386846032mshffd07fea16699c2p103a45jsn15c59ab9be78',
          'X-RapidAPI-Host': 'cors-proxy4.p.rapidapi.com'
        }
      }
    )
    .then(res=>{
      console.log(res)
      if(res.data.message.body.track_list.length === 0){
        dispatch({
          type: 'NO_WAY'
        })
        
      }
      else{
        dispatch({
          type:'SEARCH_TRACKS',
          payload:res.data.message.body.track_list,
  
        })
      }
    })
    .catch(err =>console.log(err));
  }

  render(){

  // const [trackTitle,setTrackTitle] = useState(''); cant use hooks because only a class component can use the bind() function.
  return (
    <Consumer>
        {value=>{
          const {dispatch} = value;
                return(

    <Container  component="form"  maxWidth="sm">
            <Typography
              component="h1"
              variant="h2"
              align="center"
              color="text.primary"
              gutterBottom
            >
               Lyric finder 
            </Typography>
            <Typography variant="h5" align="center" color="text.secondary" paragraph>
              Find all your favorite song lyrics in one place.
            </Typography>
            <TextField fullWidth label="Search Lyrics" name='trackTitle' value={this.state.trackTitle} onChange={this.onChange} placeholder='Search Lyrics' id="fullWidth" />
            <Stack
              sx={{ pt: 4 }}
              direction="row"
              spacing={2}
              justifyContent="center"
            >
              <Button variant="contained" onClick={this.findTrack.bind(this,dispatch)} type='submit'>Search Now
              <LyricsIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 ,ml:1}} />
              </Button>
              <Button variant="outlined"
               onClick={this.clearSearch}
               >Clear Search</Button>
            </Stack>
{/* theres a stack here */}
          </Container>
                )
        }}
    </Consumer>
  )
}
}

export default Search;
